:root {
  --var-color-primary: #ff980d;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.about-film-modal {
  height: 100%;
  width: 100%;
  z-index: 999;
  opacity: 1;
  background-color: #0003;
  padding: 20px;
  transition: opacity .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.about-film-modal.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.about-film-modal .container {
  overflow-y: auto;
}

.modal-body {
  width: 90%;
  background-color: #fff;
  border-radius: 20px;
  padding: 48px 20px 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -30%);
}

@media screen and (min-width: 480px) {
  .modal-body {
    width: 65%;
    padding: 48px 20px 40px;
    transform: translate(-50%, -30%);
  }
}

